<template><section><html><head></head><body><h1>CircleGeometry, CircleOutlineGeometry</h1>
<ul>
<li>The <code v-pre="">vc-geometry-circle</code> component is used to load a geometry that describes a circle on the ellipsoid.</li>
<li>The <code v-pre="">vc-geometry-outline-circle</code> component is used to load a geometry that describes the outline of a circle on the ellipsoid.</li>
<li>It needs to be used as a sub-component of <code v-pre="">vc-instance-geometry</code>, can be mounted on <code v-pre="">vc-primitive</code> or <code v-pre="">vc-primitive-ground</code>.</li>
</ul>
<h2>Example</h2>
<h3>Load CircleGeometry</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" @left_click="LEFT_CLICK">
        <vc-primitive-ground :appearance="appearance">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes">
            <vc-geometry-circle ref="circle" :center="center" :radius="250000"></vc-geometry-circle>
          </vc-instance-geometry>
        </vc-primitive-ground>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributes">
            <vc-geometry-outline-circle ref="circleOutline" :center="centerOutline" :radius="250000"></vc-geometry-outline-circle>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
